@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(sports) {
  overflow: hidden;
  background-color: $--background-color-page;

  @include e(banner) {
    height: 300px;
  }

  @include e(content) {
    width: $--content-width;
    margin: 60px auto;
  }

  @include e(item) {
    position: relative;
    float: left;
    width: 594px;
    height: 260px;
    background: no-repeat center/140px auto;

    @include background-image(url(~@/assets/img/common/default-img.png));

    & + & {
      margin-left: $--content-width - 594 * 2;
    }
  }

  @include e(info) {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    padding-left: 26px;
  }

  @include e(title) {
    margin-top: 45px;
    font-size: 24px;
    color: #fff;
  }

  @include e(button) {
    position: absolute;
    bottom: 30px;
  }
}

@include b(games) {
  height: 176px;
  padding-top: 25px;
  overflow: hidden;
  background-color: #181818;
  transition: all 0.3s ease;

  @include e(inner) {
    width: $--content-width;
    margin: 0 auto;
  }

  @include e(slide) {
    position: relative;

    & + & {
      &::before {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        width: 1px;
        height: 100%;
        content: '\20';
        border-right: 1px solid #2d2d2d;
      }
    }

    &:hover {
      @include e(mask) {
        visibility: visible;
        opacity: 1;
      }

      &::before {
        z-index: 3;
      }
    }
  }

  @include e(item) {
    width: 300px;
    font-size: 12px;
    line-height: 16px;
    color: #999;
    text-align: center;
  }

  @include e(mask) {
    @extend %hover-mask;
  }

  @include e(button) {
    @include button(150px, 36px);
    @include button-solid();

    position: relative;
    top: 50%;
    margin-top: -18px;
    vertical-align: top;
  }

  @include e(title) {
    font-size: 14px;
    line-height: 22px;
    color: #fff;
  }

  @include e(date) {
    color: #828282;
  }

  @include e(info) {
    margin-top: 12px;
  }

  @include e(icon-wrapper) {
    position: relative;
    height: 44px;
    overflow: hidden;
    font-size: 0;
    text-align: center;
  }

  @include e(team) {
    margin-top: 6px;
    line-height: 20px;
  }

  @include e(team-wrapper) {
    display: inline-block;
    vertical-align: top;

    &:first-child,
    &:last-child {
      width: 120px;
    }

    &:nth-child(2) {
      width: 60px;
      padding-top: 22px;
    }
  }

  &.hidden {
    margin-top: -176px;
    visibility: hidden;
    opacity: 0;
  }

  :global(.swiper-slide-active) {
    &::before {
      display: none;
    }
  }
}
