@import "variables.scss";
@import "functions.scss";
@import "bem.scss";

@mixin mask($z-index:1,$background-opacity:0.5) {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $z-index;
  background-color: rgba(0, 0, 0, $background-opacity);
}

@mixin _background-image($url,$useWebp,$isCssModule) {
  background-image: url($url);

  @if $useWebp {
    @at-root (without: all) {
      @if $isCssModule {
        :global(.support-webp) & {
          background-image: url($url + '.webp');
        }
      }

      @else {
        .support-webp & {
          background-image: url($url + '.webp');
        }
      }
    }
  }
}

@mixin background-image($url,$useWebp:$--use-webp,$isCssModule:true) {
  @if containsModifier($url, 'url(') {
    $url: str-slice($url, 5, -2);
  }

  @include _background-image($url, $useWebp, $isCssModule);
}

@mixin disabled($opacity:0.7) {
  filter: alpha(opacity=$opacity*100);
  opacity: $opacity;
}

@mixin button($width:100%,$height:40px,$font-size:16px,$radius:$--border-radius-base) {
  width: $width;
  height: $height;
  font-size: $font-size;
  line-height: $height;
  border-radius: $--border-radius-base;
}

@mixin button-solid($color:$--color-white) {
  color: $color;

  @include button-background-solid();
}

@mixin button-hollow($color:$--color-primary) {
  color: $color;

  @include button-background-hollow();
}

@mixin button-background-solid($hover:true) {
  background: $--color-primary linear-gradient(90deg, $--color-primary, $--color-primary-dark);

  @if ($hover) {
    &:hover {
      background: $--color-primary-dark;
    }
  }
}

@mixin button-background-hollow($hover:true) {
  border: 1px solid $--border-color-button;

  @if ($hover) {
    &:hover {
      background-color: rgba(203, 163, 82, 0.1);
    }
  }
}

%hover-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  text-align: center;
  visibility: hidden;
  background-color: rgba(10, 10, 10, 0.7);
  opacity: 0;
  transition: all 0.2s ease;
}
